import { OnInit, OnChanges, Directive, Input, Output, EventEmitter, Injector, ApplicationRef, ElementRef, ViewContainerRef, ReflectiveInjector, ComponentFactoryResolver, ComponentRef, OnDestroy } from '@angular/core';

import { ColorPickerService } from './color-picker.service';
import { ColorPickerComponent } from './color-picker.component';

import { SliderPosition, SliderDimension} from './helpers';


export class ColorPickerDirective implements OnInit, OnChanges, OnDestroy {
     colorPicker: string;
     cpToggle: boolean;
     cpPosition: string = 'right';
     cpPositionOffset: string = '0%';
     cpPositionRelativeToArrow: boolean = false;
     cpOutputFormat: string = 'hex';
     cpPresetLabel: string = 'Preset colors';
     cpPresetColors: Array<string>;
     cpCancelButton: boolean = false;
     cpCancelButtonClass: string = 'cp-cancel-button-class';
     cpCancelButtonText: string = 'Cancel';
     cpOKButton: boolean = false;
     cpOKButtonClass: string = 'cp-ok-button-class';
     cpOKButtonText: string = 'OK';
     cpFallbackColor: string = '#fff';
     cpHeight: string = 'auto';
     cpWidth: string = '230px';
     cpIgnoredElements: any = [];
     cpDialogDisplay: string = 'popup';
     cpSaveClickOutside: boolean = true;
     cpAlphaChannel: string = 'enabled';
     cpUseRootViewContainer: boolean = false;

     cpInputChange = new EventEmitter<any>(true);

     cpToggleChange = new EventEmitter<boolean>(true);

     cpSliderChange = new EventEmitter<any>(true);
     cpSliderDragEnd = new EventEmitter<string>(true);
     cpSliderDragStart = new EventEmitter<string>(true);

     colorPickerCancel = new EventEmitter<string>(true);
     colorPickerSelect = new EventEmitter<string>(true);
     colorPickerChange = new EventEmitter<string>(false);

    private dialog: any;
    private created: boolean;
    private ignoreChanges: boolean = false;
    private cmpRef: ComponentRef<ColorPickerComponent>;

    constructor(private injector: Injector, private cfr: ComponentFactoryResolver,
      private appRef: ApplicationRef, private vcRef: ViewContainerRef, private elRef: ElementRef,
      private service: ColorPickerService)
    {
        this.created = false;
    }

    ngOnChanges(changes: any): void {
        if (changes.cpToggle) {
            if (changes.cpToggle.currentValue) this.openDialog();
            if (!changes.cpToggle.currentValue && this.dialog) this.dialog.closeColorPicker();
        }
        if (changes.colorPicker) {
            if (this.dialog && !this.ignoreChanges) {
                if (this.cpDialogDisplay === 'inline') {
                    this.dialog.setInitialColor(changes.colorPicker.currentValue);
                }
                this.dialog.setColorFromString(changes.colorPicker.currentValue, false);

            }
            this.ignoreChanges = false;
        }
        if (changes.cpPresetLabel || changes.cpPresetColors) {
            if (this.dialog) {
                this.dialog.setPresetConfig(this.cpPresetLabel, this.cpPresetColors);
            }
        }
    }

    ngOnInit() {
        this.colorPicker = this.colorPicker || this.cpFallbackColor || 'rgba(0, 0, 0, 1)';
        /*let hsva = this.service.stringToHsva(this.colorPicker);
        if (hsva === null) hsva = this.service.stringToHsva(this.colorPicker, true);
        if (hsva == null) {
            hsva = this.service.stringToHsva(this.cpFallbackColor);
        }
        let color = this.service.outputFormat(hsva, this.cpOutputFormat, this.cpAlphaChannel);
        if (color !== this.colorPicker) {
            //setTimeout(() => {
              this.colorPickerChange.emit(color);
              this.cdr.detectChanges();
            //}, 0);
        }*/
    }

    ngOnDestroy() {
        if (this.cmpRef !== undefined) {
            this.cmpRef.destroy();
        }
    }

    openDialog() {
        this.colorPicker = this.colorPicker || this.cpFallbackColor || 'rgba(0, 0, 0, 1)';

        if (!this.created) {
            this.created = true;
            let vcRef = this.vcRef;
            if (this.cpUseRootViewContainer && this.cpDialogDisplay !== 'inline') {
              const classOfRootComponent = this.appRef.componentTypes[0];
              const appInstance = this.injector.get(classOfRootComponent);
              vcRef = appInstance.vcRef || appInstance.viewContainerRef || this.vcRef;
              if (vcRef === this.vcRef) {
                console.warn('You are using cpUseRootViewContainer, but the root component is not exposing viewContainerRef!' +
                  'Please expose it by adding \'public vcRef: ViewContainerRef\' to the constructor.');
              }
            }
            const compFactory = this.cfr.resolveComponentFactory(ColorPickerComponent);
            const injector = ReflectiveInjector.fromResolvedProviders([], vcRef.parentInjector);
            this.cmpRef = vcRef.createComponent(compFactory, 0, injector, []);
            this.cmpRef.instance.setDialog(this, this.elRef, this.colorPicker, this.cpPosition, this.cpPositionOffset,
                this.cpPositionRelativeToArrow, this.cpOutputFormat, this.cpPresetLabel, this.cpPresetColors,
                this.cpCancelButton, this.cpCancelButtonClass, this.cpCancelButtonText, this.cpOKButton,
                this.cpOKButtonClass, this.cpOKButtonText, this.cpHeight, this.cpWidth, this.cpIgnoredElements,
                this.cpDialogDisplay, this.cpSaveClickOutside, this.cpAlphaChannel, this.cpUseRootViewContainer);
            this.dialog = this.cmpRef.instance;

            if (this.vcRef !== vcRef) {
                this.cmpRef.changeDetectorRef.detectChanges();
            }
        } else if (this.dialog) {
            this.dialog.openDialog(this.colorPicker);
        }
    }

    toggle(value: boolean) {
        this.cpToggleChange.emit(value);
    }

    colorChanged(value: string, ignore: boolean = true) {
        this.ignoreChanges = ignore;
        this.colorPickerChange.emit(value);
    }

    colorCanceled() {
      this.colorPickerCancel.emit();
    }

    colorSelected(value: string) {
        this.colorPickerSelect.emit(value);
    }

    inputFocus() {
        if (this.cpIgnoredElements.filter((item: any) => item === this.elRef.nativeElement).length === 0) {
            this.openDialog();
        }
    }

    inputChange(value: string) {
      if (this.dialog) {
        this.dialog.setColorFromString(value, true);
      } else {
        this.colorPicker = value || this.cpFallbackColor || 'rgba(0, 0, 0, 1)';

        this.colorPickerChange.emit(this.colorPicker);
      }
    }

    inputChanged(event: any) {
        this.cpInputChange.emit(event);
    }

    sliderChanged(event: any) {
        this.cpSliderChange.emit(event);
    }

    sliderDragEnd(event: any) {
        this.cpSliderDragEnd.emit(event);
    }

    sliderDragStart(event: any) {
        this.cpSliderDragStart.emit(event);
    }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
    selector: '[colorPicker]',
    host: {
      '(click)': 'inputFocus()',
      '(focus)': 'inputFocus()',
      '(input)': 'inputChange($event.target.value)'
    }
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Injector, },
{type: ComponentFactoryResolver, },
{type: ApplicationRef, },
{type: ViewContainerRef, },
{type: ElementRef, },
{type: ColorPickerService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'colorPicker': [{ type: Input, args: ['colorPicker', ] },],
'cpToggle': [{ type: Input, args: ['cpToggle', ] },],
'cpPosition': [{ type: Input, args: ['cpPosition', ] },],
'cpPositionOffset': [{ type: Input, args: ['cpPositionOffset', ] },],
'cpPositionRelativeToArrow': [{ type: Input, args: ['cpPositionRelativeToArrow', ] },],
'cpOutputFormat': [{ type: Input, args: ['cpOutputFormat', ] },],
'cpPresetLabel': [{ type: Input, args: ['cpPresetLabel', ] },],
'cpPresetColors': [{ type: Input, args: ['cpPresetColors', ] },],
'cpCancelButton': [{ type: Input, args: ['cpCancelButton', ] },],
'cpCancelButtonClass': [{ type: Input, args: ['cpCancelButtonClass', ] },],
'cpCancelButtonText': [{ type: Input, args: ['cpCancelButtonText', ] },],
'cpOKButton': [{ type: Input, args: ['cpOKButton', ] },],
'cpOKButtonClass': [{ type: Input, args: ['cpOKButtonClass', ] },],
'cpOKButtonText': [{ type: Input, args: ['cpOKButtonText', ] },],
'cpFallbackColor': [{ type: Input, args: ['cpFallbackColor', ] },],
'cpHeight': [{ type: Input, args: ['cpHeight', ] },],
'cpWidth': [{ type: Input, args: ['cpWidth', ] },],
'cpIgnoredElements': [{ type: Input, args: ['cpIgnoredElements', ] },],
'cpDialogDisplay': [{ type: Input, args: ['cpDialogDisplay', ] },],
'cpSaveClickOutside': [{ type: Input, args: ['cpSaveClickOutside', ] },],
'cpAlphaChannel': [{ type: Input, args: ['cpAlphaChannel', ] },],
'cpUseRootViewContainer': [{ type: Input, args: ['cpUseRootViewContainer', ] },],
'cpInputChange': [{ type: Output, args: ['cpInputChange', ] },],
'cpToggleChange': [{ type: Output, args: ['cpToggleChange', ] },],
'cpSliderChange': [{ type: Output, args: ['cpSliderChange', ] },],
'cpSliderDragEnd': [{ type: Output, args: ['cpSliderDragEnd', ] },],
'cpSliderDragStart': [{ type: Output, args: ['cpSliderDragStart', ] },],
'colorPickerCancel': [{ type: Output, args: ['colorPickerCancel', ] },],
'colorPickerSelect': [{ type: Output, args: ['colorPickerSelect', ] },],
'colorPickerChange': [{ type: Output, args: ['colorPickerChange', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
